
import VHeader from '@/components/v-header/v-header'
import IndexList from '@/components/index-list/index-list'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import { isIphoneX } from '@/assets/js/brower'
import { mapGetters } from 'vuex'
import axios from '@/api/axiosApi'

export default {
data  () {
return {
showSelectAddress: false,
showFigure: false,
publishAddress: null,
travelTime: '',
numberOfDays: '',
figureActionsheet: {
figure1: '一个人',
figure2: '情侣/夫妻',
figure3: '带孩子',
figure4: '家庭出游',
figure5: '和朋友',
figure6: '和同学',
figure7: '其他'
},
figure: '',
thePerCapitaCost: ''
}
},
directives: {
TransferDom
},
components: {
VHeader, Group, Datetime, Actionsheet, XInput, Popup, IndexList
},
computed: {
...mapGetters(['areaList'])
},
created() {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingBottom = 34
}
this.mainHeight = 0
},
mounted () {
this.mainHeight = window.innerHeight - this.$refs.header.clientHeight
},
methods: {
selectArea (area) {
this.publishAddress = area
this.showSelectAddress = false
},
setToday() {
this.travelTime = this.moment().format('YYYY-MM-DD')
},
selectFigure(key, item) {
this.figure = item
},
submit() {
if (this.publishAddress === null) {
this.$vux.toast.text('请选择出行目的地', 'middle')
return
}
axios.put(this, '/v1/playStrategy/' + this.$route.params.id, {
publishAddressId: this.publishAddress.id,
travelTime: this.travelTime,
figure: this.figure,
numberOfDays: this.numberOfDays,
thePerCapitaCost: this.thePerCapitaCost
}, () => {
this.$vux.toast.text('发布游记成功~', 'middle')
this.$router.go(-1)
})
}
}
}



